"use client";

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { useUser } from '@/context/UserContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';

interface Course {
  id: string;
  title: string;
  duration: string;
  fee: string;
  schedule: string;
  description: string;
  modules: string[];
}

export default function StudentDashboard() {
  const { user, logout, loading: userLoading } = useUser();
  const [courses, setCourses] = useState<Course[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const router = useRouter();

  useEffect(() => {
    if (!userLoading && !user) {
      router.push('/login');
      return;
    }

    if (user && user.role !== 'student') {
      // Redirect to appropriate dashboard based on role
      if (user.role === 'admin') {
        router.push('/admin');
      } else if (user.role === 'instructor') {
        router.push('/instructor');
      }
      return;
    }

    if (user && !user.approved) {
      // Student is not approved yet
      setLoading(false);
      return;
    }

    // Fetch courses for approved students
    const fetchCourses = async () => {
      try {
        const response = await fetch('/api/courses');
        if (response.ok) {
          const data = await response.json();
          setCourses(data);
        } else {
          setError('Failed to load courses');
        }
      } catch (err) {
        setError('Error loading courses');
      } finally {
        setLoading(false);
      }
    };

    if (user && user.approved) {
      fetchCourses();
    }
  }, [user, userLoading, router]);

  const handleLogout = () => {
    logout();
    router.push('/');
  };

  if (userLoading || loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <p className="text-gray-600">Loading...</p>
        </div>
      </div>
    );
  }

  if (!user) {
    return null; // Will redirect to login
  }

  if (!user.approved) {
    return (
      <div className="min-h-screen bg-gray-50">
        {/* Header */}
        <header className="bg-black text-white py-4">
          <div className="container mx-auto px-4 flex justify-between items-center">
            <div>
              <h1 className="text-2xl font-bold">The Fourth Sealing Hub</h1>
              <p className="text-gray-300">Student Portal</p>
            </div>
            <div className="flex items-center space-x-4">
              <span className="text-sm">Welcome, {user.name}</span>
              <Button 
                onClick={handleLogout}
                variant="outline" 
                className="border-white text-white hover:bg-white hover:text-black"
              >
                Logout
              </Button>
            </div>
          </div>
        </header>

        <main className="container mx-auto px-4 py-8">
          <Alert className="border-yellow-200 bg-yellow-50 max-w-2xl mx-auto">
            <AlertDescription className="text-yellow-800">
              <h3 className="font-semibold mb-2">Account Pending Approval</h3>
              <p>
                Your student account is currently pending approval from an administrator. 
                You will receive access to course materials once your account is approved.
              </p>
              <p className="mt-2">
                Please contact us at <strong>thefourthsealing@outlook.com</strong> if you have any questions.
              </p>
            </AlertDescription>
          </Alert>
        </main>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-black text-white py-4">
        <div className="container mx-auto px-4 flex justify-between items-center">
          <div>
            <h1 className="text-2xl font-bold">The Fourth Sealing Hub</h1>
            <p className="text-gray-300">Student Portal</p>
          </div>
          <div className="flex items-center space-x-4">
            <span className="text-sm">Welcome, {user.name}</span>
            <Button 
              onClick={handleLogout}
              variant="outline" 
              className="border-white text-white hover:bg-white hover:text-black"
            >
              Logout
            </Button>
          </div>
        </div>
      </header>

      {/* Navigation */}
      <nav className="bg-gray-100 py-3">
        <div className="container mx-auto px-4">
          <div className="flex space-x-6">
            <Link href="/dashboard" className="text-black font-medium border-b-2 border-black pb-1">
              My Courses
            </Link>
          </div>
        </div>
      </nav>

      <main className="container mx-auto px-4 py-8">
        {/* Welcome Section */}
        <div className="mb-8">
          <h2 className="text-3xl font-bold text-black mb-2">Welcome to Your Learning Portal</h2>
          <p className="text-gray-600">
            Access your enrolled courses and track your learning progress.
          </p>
        </div>

        {error && (
          <Alert className="border-red-200 bg-red-50 mb-6">
            <AlertDescription className="text-red-800">
              {error}
            </AlertDescription>
          </Alert>
        )}

        {/* Available Courses */}
        <section>
          <h3 className="text-2xl font-bold text-black mb-6">Available Courses</h3>
          
          {courses.length === 0 ? (
            <div className="text-center py-8">
              <p className="text-gray-600">No courses available at the moment.</p>
            </div>
          ) : (
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
              {courses.map((course) => (
                <Card key={course.id} className="border-2 border-gray-200 hover:border-black transition-colors">
                  <CardHeader>
                    <CardTitle className="text-xl text-black">{course.title}</CardTitle>
                    <CardDescription className="text-gray-600">
                      {course.description}
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-3 mb-4">
                      <div className="flex justify-between">
                        <span className="font-semibold text-black">Duration:</span>
                        <span className="text-gray-700">{course.duration}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="font-semibold text-black">Fee:</span>
                        <span className="text-gray-700">{course.fee}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="font-semibold text-black">Schedule:</span>
                        <span className="text-gray-700 text-sm">{course.schedule}</span>
                      </div>
                    </div>

                    {/* Course Modules Preview */}
                    <div className="mb-4">
                      <h4 className="font-semibold text-black mb-2">Course Modules:</h4>
                      <ul className="text-sm text-gray-600 space-y-1">
                        {course.modules.slice(0, 3).map((module, index) => (
                          <li key={index} className="flex items-start">
                            <span className="mr-2">•</span>
                            <span>{module}</span>
                          </li>
                        ))}
                        {course.modules.length > 3 && (
                          <li className="text-gray-500 italic">
                            +{course.modules.length - 3} more modules...
                          </li>
                        )}
                      </ul>
                    </div>

                    <Link href={`/course/${course.id}`}>
                      <Button className="w-full bg-black text-white hover:bg-gray-800">
                        View Course Details
                      </Button>
                    </Link>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </section>

        {/* Payment Information */}
        <section className="mt-12 bg-white p-6 rounded-lg border-2 border-gray-200">
          <h3 className="text-xl font-bold text-black mb-4">Payment Information</h3>
          <div className="grid md:grid-cols-2 gap-6">
            <div>
              <h4 className="font-semibold text-black mb-2">Payment Options</h4>
              <ul className="text-gray-700 space-y-1">
                <li>• Full payment discount: 10% off total fee</li>
                <li>• Installment plans available</li>
                <li>• Monthly payment options</li>
                <li>• Corporate training packages</li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold text-black mb-2">Contact for Enrollment</h4>
              <p className="text-gray-700">
                Email: <strong>thefourthsealing@outlook.com</strong>
              </p>
              <p className="text-gray-700">
                Website: <strong>www.thefourthsealing.com</strong>
              </p>
            </div>
          </div>
        </section>
      </main>
    </div>
  );
}
